import React, { Component } from 'react';

function validateEmail(email) {
    var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return re.test(String(email).toLowerCase());
}

class Contact extends Component {
    constructor(props) {
        super(props);
        this.state = {
            name: '',
            email: '',
            message: '',
            emailValid: ''
        }

        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleChange(e) {
        e.target.className == 'name' ? this.setState({
            name: e.target.value
        }) : e.target.className == 'email' ? this.setState({
            email: e.target.value
        }) : this.setState({
            message: e.target.value
        })
    }

    handleSubmit() {
        validateEmail(this.state.email) ? this.setState({
            name: '',
            email: '',
            message: '',
            emailValid: ''
        }) : this.setState({
            emailValid: 'Please enter correct email address'
        })
    }


    render() {
        return (
            <section id="Contact">

                <div id = 'SendMeMessage'>
                    <h1>Send me message</h1>
                    <p>Please fill out the quick form and we will be in 
                        touch with lightening speed
                    </p>

                    <form onSubmit = {this.handleSubmit}>
                        <input required onChange = {this.handleChange} value = {this.state.name} className = 'name' placeholder = 'Name'></input>
                        <input required onChange = {this.handleChange} value = {this.state.email} className ='email' placeholder = 'Your email address'></input>
                        <p id ='emailValid'>{this.state.emailValid}</p>
                        <textarea onChange = {this.handleChange} value = {this.state.message} className = 'message' placeholder = 'Message'></textarea>
                        <button className= 'sumbit' type = 'submit'>Submit</button>
                    </form>
                </div>

                <div id = 'GetInTouch'>
                    <h1>Get In Touch</h1>
                    <p><i class="fas fa-map-marker-alt"></i> China</p>
                    
                    <p><i class="fas fa-phone-alt"></i>(+86)18775525557</p>
                    <p><i class="far fa-envelope"></i>hunzidane@gmail.com</p>
                    <p><i class="fab fa-github"></i>https://github.com/Huys952759</p>
                </div>


            </section>
        );
    }
}

export default Contact;